import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Typography, Avatar, TextField, IconButton, Menu, MenuItem, createTheme, ThemeProvider, useTheme } from '@mui/material';
import { makeStyles } from '@mui/styles';
import MenuIcon from '@mui/icons-material/Menu';
import SendIcon from '@mui/icons-material/Send';
import Logo from '../assets/images/logo.png';

const theme = createTheme({
  palette: {
    primary: {
      main: '#2196f3', // Primary blue color
    },
    background: {
      default: '#f5f5f5', // Light gray background
    },
  },
});

const useStyles = makeStyles((theme) => ({
  appContainer: {
    display: 'flex',
    flexDirection: 'column',
    height: '100vh',
  //  backgroundColor: theme.palette.background.default,
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: theme.spacing(2),
    backgroundColor: '#fff',
    boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
  },
  headerAvatar: {
    width: 50,
    height: 50,
  },
  headerTitle: {
    fontWeight: 'bold',
    color: theme.palette.primary.main,
  },
  menuButton: {
    color: theme.palette.primary.main,
  },
  chatWindow: {
    flex: 1,
    overflowY: 'auto',
    padding: theme.spacing(3),
    backgroundColor: '#fff',
    borderRadius: 10,
    boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
  },
  chatBubble: {
    marginTop: theme.spacing(2),
    padding: theme.spacing(2),
    borderRadius: 15,
    fontSize: 16,
    lineHeight: 1.6,
    maxWidth: '80%',
    boxShadow: '0 2px 6px rgba(0, 0, 0, 0.1)',
    transition: 'transform 0.2s ease-in-out',
    '&:hover': {
      transform: 'scale(1.02)',
    },
  },
  userBubble: {
    alignSelf: 'flex-end',
    backgroundColor: theme.palette.primary.main,
    color: '#fff',
  },
  botBubble: {
    alignSelf: 'flex-start',
    backgroundColor: '#f0f0f0',
    color: '#333',
  },
  chatInputContainer: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(2),
    backgroundColor: '#fff',
    borderTop: `1px solid ${theme.palette.divider}`,
    boxShadow: '0 -2px 8px rgba(0, 0, 0, 0.1)',
  },
  chatInput: {
    flex: 1,
    border: `2px solid ${theme.palette.divider}`,
    borderRadius: 10,
    padding: theme.spacing(1.5),
    outline: 'none',
    fontSize: 16,
  },
  sendButton: {
    marginLeft: theme.spacing(2),
    backgroundColor: theme.palette.primary.main,
    color: '#fff',
    borderRadius: 10,
    width: 50,
    height: 50,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    boxShadow: '0 2px 6px rgba(0, 0, 0, 0.15)',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: '#1976d2',
    },
  },
}));

const StudentHelpdesk = () => {
  const navigate = useNavigate();
  const [messages, setMessages] = useState([]);
  const [userMessage, setUserMessage] = useState('');
  const [anchorEl, setAnchorEl] = useState(null);
  const chatWindowRef = useRef(null);
  const classes = useStyles(useTheme());

  useEffect(() => {
    if (chatWindowRef.current) {
      chatWindowRef.current.scrollTop = chatWindowRef.current.scrollHeight;
    }
  }, [messages]);

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);   
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    navigate('/adminsignin');
  };

  const handleSend = () => {
    if (userMessage.trim()) {
      setMessages([...messages, { text: userMessage, sender: 'user' }]);
      setUserMessage('');

      // Fetch response from backend and add it to the messages
      // (Assuming the backend functionality remains the same)
      // ...
    }
  };

  const handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      event.preventDefault();
      handleSend();
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Box className={classes.appContainer}>
        <Box className={classes.header}>
          <Avatar className={classes.headerAvatar} src={Logo} alt="AI StudyPal" />
          <Typography variant="h5" className={classes.headerTitle}>
            AI StudyPal
          </Typography>
          <IconButton onClick={handleMenuClick} className={classes.menuButton}>
            <MenuIcon />
          </IconButton>
          <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleMenuClose}>
            <MenuItem onClick={handleMenuClose}>Admin Login</MenuItem>
          </Menu>
        </Box>

        <Box ref={chatWindowRef} className={classes.chatWindow}>
          {messages.map((msg, index) => (
            <Box
              key={index}
              className={`${classes.chatBubble} ${msg.sender === 'user' ? classes.userBubble : classes.botBubble}`}
            >
              {msg.text}
            </Box>
          ))}
        </Box>

        <Box className={classes.chatInputContainer}>
          <TextField
            className={classes.chatInput}
            placeholder="Type your message..."
            fullWidth
            value={userMessage}
            onChange={(e) => setUserMessage(e.target.value)}
            onKeyPress={handleKeyPress}
            InputProps={{
              endAdornment: (
                <IconButton className={classes.sendButton} onClick={handleSend}>
                  <SendIcon />
                </IconButton>
              ),
            }}
          />
        </Box>
      </Box>
    </ThemeProvider>
  );
};

export default StudentHelpdesk;